@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav{
     height: 71px;
     box-shadow: 0px 1px 8px #888888;
     display: flex;
     align-items: center;
     position: fixed;
     width: 100%;
     z-index: 1;
     overflow: hidden;
     top: 0;
     background-color: white;
}

.nav img{
     height: 80%;
     cursor: pointer;
}
.icon{
     flex: 0.5 1;
}

.content {
     padding: 20px;
     display: flex;
     flex-direction: column;
     align-items: center;
   }

.downloadMsg{
     width: 95%;
     display: flex;
     align-items: center;
     justify-content: space-around;
     background: #f44336;
     height: 48px;
     padding: 0 40px;
     margin: 20px 0 35px 0;
     color: white;
     box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.6),0 2px 10px 0 rgba(0, 0, 0, 0.12);
}
.downloadText{
     flex: 1 1;
}
.links{
     display: flex;
     justify-content: center;
     align-items: center;
     margin-top: 6px;
}

img{
     height: 40px;
}

@media (max-width: 995px) {
     .downloadMsg{
          display: none;
     }
}

.loader {
     cursor: pointer;
     background-color: white;
     border: none;
     box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
     padding: 7px 25px;
     font-size: 13px;
     margin-bottom: 20px;
   }
.newsCard {
  display: flex;
  width: 100%;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  padding: 10px;
  margin-bottom: 25px;
  border-radius: 5px;
}

.newsText {
  padding: 0 0 0 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.author {
  font-size: 12px;
  font-weight: 300;
  line-height: 22px;
  color: #808290;
}

.lowerNewsText {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex: 1 1;
  padding: 10px 0;
}

.readmore {
  font-size: 12px;
  font-weight: 400;
  padding-top: 10px;
}

.link{
  text-decoration: none;
  color: black;
}

.newsImage {
  object-fit: cover;
  width: 320px;
  height: 268px;
}
.title {
  font-size: 22px;
  line-height: 27px;
  font-family: "Roboto", sans-serif;
}

.muted {
  font-weight: 100;
}

.desc {
  font-size: 16px;
  line-height: 22px;
  color: #44444d;
}

@media (max-width: 800px) {
  .newsCard {
    flex-direction: column;
    width: 100%;
  }
  .newsImage {
    width: 100%;
    height: 100%;
    padding-bottom: 20px;
  }
}
